<template>
  <div class="Index">
    <div class="container-fluid" id="body">
      <Mynav/>
      <!-- 注意 article太小了在1200px也分成一个好了 -->
      <h1 class="animated zoomInDown" id="TopText">小五 的个人网站</h1>
      <h2 class="animated zoomInDown">{{IndexData.date}}</h2>
    </div>
    <Mybody/>
    <MyFooter/>
  </div>
</template>

<script>
import Mynav from "./head/nav";
import MyFooter from "./footer/footer";

export default {
  name: "Myindex",
  data() {
    return {
      IndexData: {
        date: ""
      }
    };
  },
  components: {
    Mynav,
    MyFooter
  },
  methods: {
    timeCheck: function() {
      var MyDate = new Date().getHours();
      if (MyDate >= 5 && MyDate <= 10) {
        this.IndexData.date = "早上好,欢迎访问我的小站";
      } else if (MyDate >= 11 && MyDate <= 13) {
        this.IndexData.date = "中午好，欢迎访问我的小站";
      } else if (MyDate >= 14 && MyDate <= 18) {
        this.IndexData.date = "下午好，欢迎访问我的小站";
      } else if (MyDate >= 19 && MyDate <= 22) {
        this.IndexData.date = "晚上好，欢迎访问我的小站";
      } else if (MyDate >= 23 && MyDate <= 4) {
        this.IndexData.date = "晚上好，欢迎访问我的小站，夜深了,注意休息";
      } else {
        consol.log("给您拜个早年吧！");
      }
    }
  },
  mounted: function() {
    this.timeCheck();
  }
};
</script>

<style scoped>
#body {
  background: url("./../assets/xiawu3.jpg");
  background-size: cover;
  height: 100vh;
  margin: 0;
  padding: 0;
}

#TopText {
  margin: 30vh auto;
  font-size: 4rem;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-weight: 500;
}

h2 {
  position: relative;
  bottom: 25vh;
  font-size: 18px;
  font-weight: 550;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  letter-spacing: 0.5vw;
}

.zoomInDown {
  color: #e8eaed;
  animation-duration: 2s; /*动画持续时间*/
  animation-delay: 0; /*动画延迟时间，多个元素，延迟时间要依次累加*/
  animation-iteration-count: 1; /*动画执行次数*/
  animation-fill-mode: both; /*---设置对象状态为动画结束或开始的状态;*/
}
</style>
